import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import cx from "classnames";

const ExternalLink = ({ children, className, href, text, icon, externalIcon }) => (
  <a className={cx(className)} href={href} target="_blank">
    {icon && <FontAwesomeIcon className="mr-1" icon={icon} />}
    {children}
    {text && <span className="mr-1">{text}</span>}
    {externalIcon && <FontAwesomeIcon icon="external-link-alt" />}
  </a>
);

export default ExternalLink;
